import React, { useState , useContext} from "react";
import { Link } from 'react-router-dom';
import './Join.css'
import { socket } from "../socket";
import SessionContext from '../SessionContext'
import { Redirect } from "react-router";
import {isMobile} from 'react-device-detect';
import snadesImg from "../../assets/images/snades";

const Join = () => { 

    const [name, setName] = useState('');
    const [room, setRoom] = useState('');

    const { session, setSession } = useContext(SessionContext)

    const CreateUser = () => {
        socket.emit('join', { name, roomName: room }, (error, roomData) => {
            if(error) {
              alert(error);
              return
            }
            console.log("Entramos na sala! Aqui esto os detalhes :) Atualizando sesso", roomData)
            setSession({ user: session.user, roomData: roomData })
        });
        console.log('createUser() em Join.js disparou socket.emit("join")')
    }

    // Redireciona usurio para GameRoom se j houver alguma sala
    if (session && session.roomData) {
        console.log("J estamos em uma sala! Redirecionando para GameRoom", session.roomData)
        return <Redirect to="/GameRoom"/>
    }

    const renderContent = () => {
        if (isMobile) {
            return <div><img id="snades" src={snadesImg} alt="snades"/>           
            Se liga, por enquanto s d pra jogar no computador. Quando tiver rolando no celular eu te aviso!</div>
        }
        return <div className="joinOuterContainer">
                <div className="joinInnerContainer">
                    <div class="wordartblues"><span class="text">Verso de Teste!</span></div>
                    <h1 className="heading">Ol!</h1>
                    
                    <div><input placeholder="Escreve um apelido maroto..." className="joinInput mt-20" type="text" onChange={(event) => setName(event.target.value)} /></div>
                    <div><input placeholder="Qual o nome da sala?" className="joinInput mt-20" type="text" onChange={(event) => setRoom(event.target.value)} /></div>
                    <Link onClick={event => (!name || !room) ? event.preventDefault() : CreateUser()}  to={`/GameRoom`}> 
                        <button className="button mt-20" type="submit">Manda bala</button>
                    </Link>
                </div>
            </div>            
    }
    
    return (
        <>
        {renderContent()}
        </>
    )
}

export default Join